@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html{
  background-color: #fff;
  font:normal 16px/1.5 sans-serif;
  color: #333;
}

h3{
  font: normal 32px/1.5 'Open Sans', sans-serif;
  color: #2c3e50;
  margin: 50px 0;
  text-align: center;
}


.container{
  width: 1280px;
  height: 720px;
  margin: 50px auto;
  /*padding: 20px;*/
  background-color: #efefef;
  position: relative;
}

.canvas {
  position: absolute;
  top: 0;
  left: 0;
  width: 1280px;
  height: 720px;
  background-color: rgba(0,0,0,1);
}

.image {
  position: absolute;
  top: 0;
  left: 0;
  width: 1280px;
  height: 720px;
}

.point {
  position: absolute;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background: -webkit-radial-gradient(#448AFF, transparent);
  background: radial-gradient(#448AFF, transparent);
  border: none;
  border-radius: 50px;
  width: 30px;
  height: 30px;
}

.plus {
  position: absolute;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background: none;
  border: none;
  padding: 0;
  font-size: 30px;
  color: red;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

