@import url('https://fonts.googleapis.com/css?family=Open+Sans:400,700');

*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html{
  background-color: #fff;
  font:normal 16px/1.5 sans-serif;
  color: #333;
}

h3{
  font: normal 32px/1.5 'Open Sans', sans-serif;
  color: #2c3e50;
  margin: 50px 0;
  text-align: center;
}


.container{
  width: 1280px;
  height: 720px;
  margin: 50px auto;
  /*padding: 20px;*/
  background-color: #efefef;
  position: relative;
}

.canvas {
  position: absolute;
  top: 0;
  left: 0;
  width: 1280px;
  height: 720px;
  background-color: rgba(0,0,0,1);
}

.image {
  position: absolute;
  top: 0;
  left: 0;
  width: 1280px;
  height: 720px;
}

.point {
  position: absolute;
  transform: translate(-50%, -50%);
  background: radial-gradient(#448AFF, transparent);
  border: none;
  border-radius: 50px;
  width: 30px;
  height: 30px;
}

.plus {
  position: absolute;
  transform: translate(-50%, -50%);
  background: none;
  border: none;
  padding: 0;
  font-size: 30px;
  color: red;
  user-select: none;
}
